body {
  margin: 0;
  font-family: "PingFang SC", -apple-system, BlinkMacSystemFont, 'SF Pro Text', 'SF Pro Display', 'Helvetica Neue', Arial, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', '微软雅黑', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #167c60;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #243127;
}

/* EDUPAL 新配色方案 */
:root {
  --edupal-primary: #167c60;
  --edupal-secondary: #243127;
  --edupal-accent: #1a6b52;
  --edupal-light: #d4edda;
}

/* Loading animation */
.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}

/* 首页特殊样式 */
.homepage-container {
  background: linear-gradient(135deg, #243127 0%, #2d3c2f 25%, #1a6b52 50%, #167c60 75%, #243127 100%);
  background-size: 400% 400%;
  animation: gradientShift 8s ease infinite;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* 卡片悬停效果 */
.feature-card {
  background: rgba(255,255,255,0.1) !important;
  border: 1px solid rgba(255,255,255,0.2) !important;
  backdrop-filter: blur(10px);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.feature-card:hover {
  transform: translateY(-12px) scale(1.02);
  background: rgba(255,255,255,0.2) !important;
  box-shadow: 0 20px 40px rgba(0,0,0,0.15);
}

/* 按钮动画效果 */
.edupal-button-primary {
  background: rgba(255,255,255,0.95);
  border: none;
  color: #167c60;
  font-weight: bold;
  border-radius: 28px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.1);
  transition: all 0.3s ease;
}

.edupal-button-primary:hover {
  background: white;
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0,0,0,0.15);
}

.edupal-button-secondary {
  background: transparent;
  border: 2px solid rgba(255,255,255,0.8);
  color: white;
  border-radius: 28px;
  transition: all 0.3s ease;
}

.edupal-button-secondary:hover {
  background: rgba(255,255,255,0.1);
  border-color: white;
  color: white;
  transform: translateY(-2px);
}

/* 统计卡片样式 */
.stat-card {
  background: rgba(255,255,255,0.15) !important;
  border: 1px solid rgba(255,255,255,0.2) !important;
  backdrop-filter: blur(10px);
  border-radius: 16px !important;
  transition: all 0.3s ease;
}

.stat-card:hover {
  background: rgba(255,255,255,0.25) !important;
  transform: translateY(-4px);
}

/* 文字发光效果 */
.text-glow {
  text-shadow: 0 0 10px rgba(255,255,255,0.3);
}

/* 响应式设计 */
@media (max-width: 768px) {
  .ant-layout-content {
    margin-left: 0 !important;
  }
  
  .ant-layout-sider {
    position: fixed !important;
    z-index: 999;
  }
  
  .homepage-hero-title {
    font-size: 36px !important;
  }
  
  .homepage-hero-subtitle {
    font-size: 18px !important;
  }
}

/* 动画类 */
.fade-in {
  animation: fadeIn 1s ease-in;
}

.fade-in-up {
  animation: fadeInUp 1s ease-out;
}

.scale-in {
  animation: scaleIn 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* 浮动动画 */
.floating {
  animation: floating 3s ease-in-out infinite;
}

@keyframes floating {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

/* 脉冲效果 */
.pulse {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.4); }
  70% { box-shadow: 0 0 0 20px rgba(255, 255, 255, 0); }
  100% { box-shadow: 0 0 0 0 rgba(255, 255, 255, 0); }
}
/* 毛玻璃 */
.glass-effect {
  backdrop-filter: blur(20px);
  background: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}
.all-white-text, 
.all-white-text * { 
  color: rgba(255, 255, 255, 0.958) !important; /* 使用 !important 覆盖 Antd 的默认样式 */
}
.button-tm {
  background-color: #167c60 !important;
  border: 1px solid #167c60 !important;
}

/* 1. 覆盖表格最外层容器和表格本体的背景色 */
.custom-transparent-table.ant-table-wrapper,
.custom-transparent-table .ant-table {
  background: transparent !important;
}

/* 2. 覆盖表头、表格单元格和行的背景色 */
.custom-transparent-table .ant-table-thead > tr > th, /* 表头 */
.custom-transparent-table .ant-table-tbody > tr > td,  /* 单元格 */
.custom-transparent-table .ant-table-tbody > tr { /* 表格行 */
  background: transparent !important;
}

/* 3. 覆盖鼠标悬停/选中时的背景色（可选，但推荐） */
.custom-transparent-table .ant-table-tbody > tr.ant-table-row-hover:hover > td {
  background: rgba(255, 255, 255, 0.1) !important; /* 可以设为半透明以保留玻璃效果 */
}

/* 4. 覆盖分页器背景（如果存在） */
.custom-transparent-table .ant-pagination {
  background: transparent !important;
}
.custom-transparent-table .ant-table-thead > tr > th {
  color: white !important; /* 强制表头文字为白色 */
  /* 如果表头需要一个半透明背景，可以在这里设置 */
  /* background: rgba(255, 255, 255, 0.1) !important; */ 
}

/* 2. 确保表格内容文字也是白色 (如果您没有使用 all-white-text 类的话) */
.custom-transparent-table .ant-table-tbody > tr > td {
  color: white !important; 
}

.llm-custom-placeholder input::placeholder {
    font-family: "Microsoft YaHei", "Heiti SC", sans-serif !important;
    font-size: 13px !important;  /* 设置较小的字号 (Large 默认是 16px) */
    opacity: 0.7;                /* 可选：调整透明度 */
  }

.white-placeholder-textarea::placeholder {
    color: rgba(255, 255, 255, 0.95) !important; /* 推荐使用半透明白色，避免和输入内容混淆 */
  }

.save-as-default-checkbox.ant-checkbox-checked .ant-checkbox-inner {
    background-color: #1A4D33 !important;
    border-color: #1A4D33 !important;
}
.save-as-default-checkbox.ant-checkbox-checked:hover .ant-checkbox-inner {
    background-color: #1A4D33 !important;
    border-color: #1A4D33 !important;
}